import React, { useState, useEffect, useMemo } from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler, TimeSeriesScale,
} from 'chart.js';
import 'chartjs-adapter-date-fns';
import { sub, startOfDay } from 'date-fns';
import annotationPlugin from 'chartjs-plugin-annotation';
import { getWidgetData } from '../../apiConfig';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler, TimeSeriesScale, annotationPlugin);

const formatMarketCap = (num) => {
    if (!num || num === 0) return 'N/A';
    if (num >= 1e12) return (num / 1e12).toFixed(2) + 'T';
    if (num >= 1e9) return (num / 1e9).toFixed(2) + 'B';
    if (num >= 1e6) return (num / 1e6).toFixed(2) + 'M';
    return num.toFixed(2);
};
const formatStat = (num) => (num === null || num === undefined) ? 'N/A' : Number(num).toFixed(2);
const getChangeColor = (change) => change > 0 ? 'text-green-600' : change < 0 ? 'text-red-600' : 'text-gray-600';

const timeframes = [
    { label: '1M', days: 30 }, { label: '6M', days: 180 }, { label: 'YTD', days: 'YTD' },
    { label: '1Y', days: 365 }, { label: '5Y', days: 365 * 5 }, { label: 'Max', days: 99999 },
];

const MarketChart = ({ ticker, chartTitle }) => {
    const [widgetData, setWidgetData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [activeTimeframe, setActiveTimeframe] = useState('1Y');

    useEffect(() => {
        if (!ticker) return;
        const fetchData = async () => {
            setLoading(true); setError(null);
            try {
                const res = await getWidgetData(ticker);
                if (res.data && res.data.quoteData) setWidgetData(res.data);
                else setError(`Data unavailable for ${ticker}`);
            } catch (err) { setError('Market data unavailable.'); }
            finally { setLoading(false); }
        };
        fetchData();
    }, [ticker]);

    const processedChartData = useMemo(() => {
        if (!widgetData?.historicalData?.length) return null;
        const { historicalData } = widgetData;
        let filtered = historicalData;
        const now = startOfDay(new Date());
        const frame = timeframes.find(t => t.label === activeTimeframe);
        if (frame && frame.label !== 'Max') {
            const cutoff = frame.days === 'YTD' ? startOfDay(new Date(now.getFullYear(), 0, 1)) : startOfDay(sub(now, { days: frame.days }));
            filtered = historicalData.filter(p => new Date(p.priceDate) >= cutoff);
        }
        if (filtered.length === 0 && historicalData.length > 0) filtered = historicalData.slice(-1); // Show at least last point if timeframe empty

        const prices = filtered.map(p => p.closePrice);
        const color = (prices[prices.length - 1] >= prices[0]) ? 'rgb(22, 163, 74)' : 'rgb(220, 38, 38)';
        return {
            labels: filtered.map(p => p.priceDate),
            datasets: [{ label: 'Price', data: prices, borderColor: color, backgroundColor: color.replace('rgb', 'rgba').replace(')', ', 0.1)'), borderWidth: 2, fill: true, pointRadius: 0, tension: 0.1 }]
        };
    }, [widgetData, activeTimeframe]);

    const chartOptions = useMemo(() => {
        const prev = widgetData?.quoteData?.previousClose;
        return {
            responsive: true, maintainAspectRatio: false,
            plugins: {
                legend: { display: false },
                annotation: { annotations: { prevClose: { type: 'line', yMin: prev, yMax: prev, borderColor: 'rgb(156, 163, 175)', borderWidth: 1, borderDash: [6, 6], label: { content: `Prev: ${prev}`, enabled: true, position: 'start', color: '#6b7280', backgroundColor: 'rgba(255,255,255,0.8)', font: { size: 10 }, yAdjust: -10 } } } },
                tooltip: { mode: 'index', intersect: false, callbacks: { label: (c) => `Price: $${Number(c.parsed.y).toFixed(2)}` } }
            },
            scales: { x: { type: 'timeseries', time: { unit: 'month', displayFormats: { month: 'MMM yy' } }, ticks: { maxTicksLimit: 6 }, grid: { display: false } }, y: { grid: { color: '#f3f4f6' } } }
        };
    }, [widgetData]);

    if (loading) return <div className="p-4 h-96 flex items-center justify-center text-gray-400 text-sm">Loading...</div>;
    if (error || !widgetData) return <div className="p-4 h-96 flex items-center justify-center text-red-500 text-sm">{error || 'No data'}</div>;

    const q = widgetData.quoteData || {};
    const changeColor = getChangeColor(q.changeAmount);

    return (
        <div className="p-4 w-full font-sans">
            {/* Header - Defensive rendering */}
            <div className="mb-3">
                <div className="text-xl font-medium text-gray-900">{chartTitle || ticker}</div>
                <div className="flex items-baseline space-x-2 mt-1">
                    <h2 className="text-3xl font-bold text-gray-900">{formatStat(q.currentPrice)}</h2>
                    <span className="text-sm font-medium text-gray-500">USD</span>
                </div>
                <div className={`text-base font-medium ${changeColor} flex items-center`}>
                    <span>{q.changeAmount >= 0 ? '+' : ''}{formatStat(q.changeAmount)}</span>
                    <span className="ml-1">({q.changePercent >= 0 ? '+' : ''}{formatStat(q.changePercent)}%)</span>
                </div>
                <div className="text-xs text-gray-400 mt-1">
                    Updated: {q.lastUpdated ? new Date(q.lastUpdated).toLocaleString() : 'Just now'}
                </div>
            </div>

            {/* Tabs */}
            <div className="flex items-center space-x-4 border-b border-gray-100 mb-4 overflow-x-auto pb-1" style={{ scrollbarWidth: 'none' }}>
                {timeframes.map((t) => (
                    <button key={t.label} onClick={() => setActiveTimeframe(t.label)} className={`text-sm font-medium py-2 whitespace-nowrap relative ${activeTimeframe === t.label ? 'text-blue-600 after:absolute after:bottom-0 after:left-0 after:w-full after:h-0.5 after:bg-blue-600' : 'text-gray-500 hover:text-gray-800'}`}>{t.label}</button>
                ))}
            </div>

            {/* Chart */}
            <div className="h-[250px] w-full mb-6">
                {processedChartData ? <Line options={chartOptions} data={processedChartData} /> : <div className="h-full flex items-center justify-center text-gray-400 text-sm">No chart data</div>}
            </div>

            {/* Summary Grid */}
            <div className="grid grid-cols-2 gap-x-8 gap-y-3 text-sm">
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">Prev close</span><span className="font-medium">{formatStat(q.previousClose)}</span></div>
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">Day range</span><span className="font-medium">{formatStat(q.dayLow)} - {formatStat(q.dayHigh)}</span></div>
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">Open</span><span className="font-medium">{formatStat(q.openPrice)}</span></div>
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">52-wk range</span><span className="font-medium">{formatStat(q.fiftyTwoWeekLow)} - {formatStat(q.fiftyTwoWeekHigh)}</span></div>
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">Mkt cap</span><span className="font-medium">{formatMarketCap(q.marketCap)}</span></div>
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">P/E ratio</span><span className="font-medium">{formatStat(q.peRatio)}</span></div>
                <div className="flex justify-between border-b border-gray-100 pb-1"><span className="text-gray-500">Div yield</span><span className="font-medium">{formatStat(q.dividendYield)}%</span></div>
            </div>
        </div>
    );
};

export default MarketChart;